.sidebar {
    box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.1), 0 10px 40px 0 rgba(29, 33, 38, 0.13);
    background: #fff;
    position: fixed;
    top: 0;
    bottom: 0;
    left: -100%;
    z-index: 100;
    width: 317px;
    padding: 16px 24px 16px 70px;
    display: flex;
    flex-direction: column;
    gap: 23px;
    transition: all 0.3s ease-in-out;
}

.sidebar.open {
    left: 0;
}

.background {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 100;
    background: #000;
    opacity: 0.3;
    transition: all 0.3s ease-in-out;
}

.nav {
    display: flex;
    flex-direction: column;
    gap: 45px;
}

.nav a {
    font-weight: 600;
    font-size: 22px;
    color: #000;
    text-decoration: none;
    transition: all 0.3s ease-in-out;
}

.nav a:hover {
    color: #58acff;
}

.nav a:active {
    color: #3d6b99;
}

.nav a.active {
    text-decoration: underline;
    text-decoration-skip-ink: none;
    color: #0059b2;
}
.close {
    align-self: flex-end;
    width: 24px;
    height: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: transparent;
    border: none;
    cursor: pointer;
}

