@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

.tech {
    width: 50vw;
    line-height: 150%;
    color: #000;
}

.developer {
    line-height: 150%;
    color: #000;
    text-transform: capitalize;
}

.techHeader {
    width: 50vw;
    font-weight: 600;
    font-size: 14px;
    color: #000;
    text-transform: capitalize;
    display: flex;
    align-items: center;
    gap: 5px;
}

.techHeader span {
    display: flex;
}

.techHeader span:hover {
    cursor: pointer;
}

.developerHeader {
    font-weight: 600;
    font-size: 14px;
    color: #000;
    text-transform: capitalize;
    display: flex;
    align-items: center;
    gap: 5px;
}

.developerHeader span {
    display: flex;
}

.developerHeader span:hover {
    cursor: pointer;
}

.row {
    display: flex;
    padding: 12px 17px;
}

.row:not(:last-child) {
    border-bottom: 1px solid #e5e5e5;
}

.rowHeader {
    display: flex;
    background-color: #e5e5e5;
    padding: 11px 17px 9px;
}

.loading {
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(255, 255, 255, 0.5);
    z-index: 1000;
    justify-content: center;
    align-items: center;
}

.spinner {
    border: 8px solid #f3f3f3;
    border-top: 8px solid #3498db;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    animation: spin 1s linear infinite;
}
