.item {
    padding: 15px 17px 16px;
    border-bottom: 1px solid #e5e5e5;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
}

.item:last-of-type {
    border-bottom: none;
}

.users {
}

.buttonsContainer {
    align-self: flex-end;
    display: flex;
    gap: 12px;
}

.container {
    width: 600px;
    display: flex;
    flex-direction: column;
    gap: 18px;
}

.thead {
    background-color: #E5E5E5;
    font-weight: 600;
    font-size: 14px;
    color: #000;
}

.nameCol {
    font-weight: 300;
    font-size: 14px;
    color: #000;
}

.ageCol {
    font-weight: 700;
    font-size: 14px;
    color: #000;
}