.input:focus {
}

.input {
    border: 1px solid #D1D1D1;
    background: transparent;
    width: 372px;
    border-radius: 5px;
    min-height: 36px;
}

.inputWrapper {
    display: flex;
    flex-direction: column-reverse;
    gap: 5px;
}

.superInput {
    border: 1px solid #d9d9d9;
    border-radius: 2px;
    width: 468px;
    height: 36px;
    font-weight: 400;
    font-size: 18px;
    color: #000;
    padding: 7px 12px 8px;
    margin-bottom: 11px;
}

.errorInput {
    border-color: #cc1439;
}

.errorInput:focus {
}

.error {
    color: #cc1439;
    font-size: 10px;
}
