.loader {
    /*свг и/или анимация*/
    /*margin: 0 auto;*/
    width: 108px;
    height: 108px;
    border: 5px solid #512DE4;
    border-top: 5px solid #fff;
    border-radius: 50%;
    animation: spin 1s infinite linear;
    margin-top: 15px;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
